import React from "react";
import { ZlabelInput, zTool } from "zerod";
const { addClass, removeClass } = zTool;
import { Row, Col, Button, Icon, message } from "antd";
import HOC from "@/HOC/load-HOC.js";
const { AmdDocHOC } = HOC;
import doc from "zerod/components/ZlabelInput/doc.md";
export default AmdDocHOC(doc, {
	demo1: function() {
		class Myjavascript extends React.PureComponent {
			state = {
				labelValue: null,
			};
			onChange = (value, e) => {
				this.setState({
					labelValue: value,
				});
			};
			render() {
				return <ZlabelInput labelPlaceholder="请输入label" valuePlaceholder="请输入value" value={this.state.labelValue} onChange={this.onChange} style={{width:"300px"}}/>;
			}
		}
		return <Myjavascript />;
	},
});
